var CELL_QUANTITIES, Cells, GRID, Immutable, Random, _, cellcodes;

Immutable = require('immutable');

_ = require('underscore');

Cells = require('../views/cell/types');

Random = require('../utility/random');

GRID = require('./grid');

CELL_QUANTITIES = Immutable.Map([[Cells.WALL, 15], [Cells.ITEM, 5]]);

cellcodes = Immutable.Map([[Cells.VOID, 0], [Cells.WALL, 1], [Cells.ITEM, 2]]);

module.exports = Object.create(null, {
  rounds_to_win: {
    enumerable: true,
    value: 3
  },
  max_score: {
    enumerable: true,
    value: function() {
      return (CELL_QUANTITIES.get(Cells.ITEM)) * this.rounds_to_win;
    }
  },
  random_reset: {
    enumerable: true,
    value: function(immutable_cellmap) {
      var grid_profile, new_cellmap, num_items, num_voids, num_walls, valid_xys;
      valid_xys = Immutable.OrderedSet(immutable_cellmap.entrySeq().filter(function(entry) {
        var cell, xy;
        xy = entry[0], cell = entry[1];
        return cell !== Cells.SNAKE;
      }).map(function(entry) {
        return entry[0];
      }));
      num_walls = CELL_QUANTITIES.get(Cells.WALL);
      num_items = CELL_QUANTITIES.get(Cells.ITEM);
      num_voids = valid_xys.size - num_walls - num_items;
      grid_profile = ((function() {
        var i, ref, results;
        results = [];
        for (_ = i = 0, ref = num_walls; 0 <= ref ? i < ref : i > ref; _ = 0 <= ref ? ++i : --i) {
          results.push(cellcodes.get(Cells.WALL));
        }
        return results;
      })()).concat(((function() {
        var i, ref, results;
        results = [];
        for (_ = i = 0, ref = num_items; 0 <= ref ? i < ref : i > ref; _ = 0 <= ref ? ++i : --i) {
          results.push(cellcodes.get(Cells.ITEM));
        }
        return results;
      })()).concat((function() {
        var i, ref, results;
        results = [];
        for (_ = i = 0, ref = num_voids; 0 <= ref ? i < ref : i > ref; _ = 0 <= ref ? ++i : --i) {
          results.push(cellcodes.get(Cells.VOID));
        }
        return results;
      })()));
      Random.shuffle(grid_profile);
      new_cellmap = immutable_cellmap.withMutations(function(mutable_cells) {
        return mutable_cells.forEach(function(cell, xy) {
          var profile_index;
          if (valid_xys.has(xy)) {
            profile_index = ((GRID.dimension - 1) * xy.x) + xy.y;
            return mutable_cells.set(xy, cellcodes.keyOf(grid_profile[profile_index]));
          } else {
            return mutable_cells.set(xy, Cells.SNAKE);
          }
        });
      });
      grid_profile = null;
      valid_xys = null;
      return new_cellmap;
    }
  }
});
