var Immutable, Snake, XY, settings,
  slice = [].slice;

Immutable = require('immutable');

XY = require('../utility/xy');

settings = require('../settings');


/*
  Represents an instance of an expandable, movable snake.

  Backed by an Immutable.OrderedSet, so .meets(xy) and move are fast.
 */

Snake = (function() {
  function Snake(xy_list) {
    this._this_frame = Immutable.OrderedSet(xy_list);
    this._last_frame = null;
    this._length = 1;
    this;
  }

  Snake.at_position = function(xy) {
    return new this([xy]);
  };

  Snake.prototype.set_motion = function(xy) {
    if (xy != null) {
      if (this._motion == null) {
        return this._motion = xy;
      } else if (XY.negate(xy) !== this._motion) {
        return this._motion = xy;
      }
    } else {
      return this._motion = null;
    }
  };

  Snake.prototype.head = function() {
    return this._this_frame.first();
  };

  Snake.prototype.move = function() {
    var new_frame, new_front, ref;
    if (this.moving()) {
      new_front = XY.add(this._this_frame.first(), this._motion);
      new_frame = this._this_frame.take(this._length - 1);
      this._last_frame = this._this_frame;
      return this._this_frame = (ref = Immutable.OrderedSet).of.apply(ref, [new_front].concat(slice.call(new_frame.toJS())));
    }
  };

  Snake.prototype.length = function() {
    return this._length;
  };

  Snake.prototype.grow = function() {
    return this._length++;
  };

  Snake.prototype.meets = function(xy) {
    return this._this_frame.has(xy);
  };

  Snake.prototype.rewind = function() {
    return this._this_frame = ((this._last_frame != null) && this._last_frame) || this._this_frame;
  };

  Snake.prototype.moving = function() {
    return this._motion != null;
  };

  Snake.prototype.toString = function() {
    return this._this_frame.toString();
  };

  return Snake;

})();

module.exports = Snake;
