var Action, Immutable, _, errors;

_ = require('underscore');

errors = require('../utility').errors;

Immutable = require('immutable');

Action = (function() {
  var __abstract_validated;

  Action.abstract_classprops = Immutable.Set(['validate_payload']);

  __abstract_validated = false;

  function Action(payload) {
    this.payload = payload;
    this;
  }

  Action.__validate_class = function() {
    if (!this.__abstract_validated) {
      if (this === Action) {
        throw new Error("Can't instantiate abstract class Action");
      } else {
        this.abstract_classprops.forEach((function(_this) {
          return function(classprop) {
            if (_this[classprop] == null) {
              throw new errors.NotImplemented("need class property `" + classprop + "`");
            }
          };
        })(this));
        return this.__abstract_validated = true;
      }
    }
  };

  Action.prototype.toString = function() {
    return this.constructor.name;
  };

  Action.value_of = function(payload) {
    this.__validate_class();
    this.validate_payload(payload);
    return this.post_value_of_hook(payload);
  };

  Action.post_value_of_hook = function(payload) {
    return new this(payload);
  };

  Action.prototype.is_a = function(klass) {
    return this instanceof klass;
  };

  return Action;

})();

module.exports = Action;
