var EmittingStore, KeyDownAction;

EmittingStore = require('./emitter').EmittingStore;

KeyDownAction = require('../actions').KeyDownAction;

module.exports = Object.create(EmittingStore, {
  add_change_listener: {
    enumerable: true,
    value: function(listener) {
      return this.addListener(this._CHANGE_EVENT, listener);
    }
  },
  _CHANGE_EVENT: {
    value: 'CHANGE'
  },
  _handle_action: {
    value: function(action) {
      if (action.is_a(KeyDownAction)) {
        return this.emit(this._CHANGE_EVENT, {
          keycode: action.keycode()
        });
      }
    }
  }
});
