var Cell, GRID, ITEMS, Immutable, LEVEL, NUM_WINS, SNAKE, STATE, STATES, Snake, XY, ref;

Snake = require('../snake');

ref = require('../settings'), GRID = ref.GRID, LEVEL = ref.LEVEL;

Cell = require('../views/cell');

XY = require('../utility/xy');

Immutable = require('immutable');

STATES = {
  reset: 'reset',
  failure: 'failure',
  success: 'success'
};

SNAKE = null;

STATE = null;

ITEMS = 0;

NUM_WINS = 0;

module.exports = Object.create(null, {

  /*
    Object that manages the state of the current game.
  
    It doesn't handle Actions, it exposes convenience methods for
    manipulating and reading game state, and lives outside the context
    of Actions.
  
    This isn't a Flux Store; it's just a bookkeeping helper class.
   */
  reset: {
    enumerable: true,
    value: function() {
      SNAKE = Snake.at_position(XY.random(GRID.dimension - 1));
      NUM_WINS = 0;
      return this.reset_round();
    }
  },
  reset_round: {
    enumerable: true,
    value: function() {
      STATE = null;
      return ITEMS = 0;
    }
  },
  collides_with: {
    value: function(xy) {
      return SNAKE.meets(xy);
    }
  },
  track_collision: {
    value: function(target, xy) {
      if (target === Cell.WALL) {
        return this._fail();
      } else if (target === Cell.SNAKE) {
        if (SNAKE.head() === xy) {
          return this._fail();
        }
      } else if (target === Cell.ITEM) {
        return this._score();
      }
    }
  },
  set_motion: {
    value: function(motion) {
      return SNAKE.set_motion(motion);
    }
  },
  move_snake: {
    enumerable: true,
    value: function() {
      return SNAKE.move();
    }
  },
  out_of_bounds: {
    enumerable: true,
    value: function() {
      var ref1, x, y;
      ref1 = SNAKE.head(), x = ref1.x, y = ref1.y;
      return Math.min(x, y) < 0 || Math.max(x, y) >= GRID.dimension;
    }
  },
  add_item: {
    enumerable: true,
    value: function() {
      return ITEMS += 1;
    }
  },
  items_left: {
    enumerable: true,
    value: function() {
      return ITEMS;
    }
  },
  over: {
    enumerable: true,
    value: function() {
      return (STATE != null) && STATE !== STATES.reset;
    }
  },
  should_reset_round: {
    enumerable: true,
    value: function() {
      return (STATE != null) && STATE === STATES.reset;
    }
  },
  failed: {
    enumerable: true,
    value: function() {
      if (STATE != null) {
        return STATE === STATES.failure;
      } else {
        return false;
      }
    }
  },
  _score: {
    value: function() {
      SNAKE.grow();
      ITEMS -= 1;
      return this._maybe_win();
    }
  },
  _maybe_win: {
    value: function() {
      if (ITEMS === 0) {
        NUM_WINS += 1;
        if (NUM_WINS === LEVEL.rounds_to_win) {
          return STATE = STATES.success;
        } else {
          return STATE = STATES.reset;
        }
      }
    }
  },
  _fail: {
    value: function() {
      SNAKE.set_motion(null);
      return STATE = STATES.failure;
    }
  }
});
