var ArrowKeys, Deactivates, Immutable, Key, Keys, PressedKeys, React;

React = require('react');

Immutable = require('immutable');

Keys = require('./types');

Key = require('./key');

Deactivates = require('./deactivates');

PressedKeys = require('../../stores').PressedKeys;

ArrowKeys = React.createClass({
  mixins: [Deactivates],
  componentWillMount: function() {
    this.setState({
      active_by_key: Immutable.Map([[Keys.LEFT, false], [Keys.UP, false], [Keys.RIGHT, false], [Keys.DOWN, false]])
    });
    return PressedKeys.add_change_listener((function(_this) {
      return function(arg) {
        var keycode;
        keycode = arg.keycode;
        return _this._on_change({
          keycode: keycode
        });
      };
    })(this));
  },
  shouldComponentUpdate: function(__, next_state) {
    return next_state.active_by_key !== this.state.active_by_key;
  },
  _on_change: function(arg) {
    var active_by_key, keycode;
    keycode = arg.keycode;
    active_by_key = this.state.active_by_key.withMutations(function(mutable_keys) {
      return mutable_keys.forEach(function(active, key) {
        return mutable_keys.set(key, key.keycode() === keycode);
      });
    });
    return this.setState({
      active_by_key: active_by_key
    });
  },
  _should_deactivate: function() {
    return this.state.active_by_key.includes(true);
  },
  _deactivate: function() {
    return this.setState({
      active_by_key: this.state.active_by_key.withMutations(function(mutable_keys) {
        return mutable_keys.forEach(function(__, key) {
          return mutable_keys.set(key, false);
        });
      })
    });
  },
  render: function() {
    return React.createElement("div", {
      "className": "arrow-keys"
    }, this.state.active_by_key.entrySeq().toJS().map(function(entry, index) {
      var active, key;
      key = entry[0], active = entry[1];
      return React.createElement(Key, {
        "pressed": active,
        "keytype": key,
        "key": index
      });
    }));
  }
});

module.exports = ArrowKeys;
