var Dispatcher, EventEmitter, NotImplemented;

EventEmitter = require('events').EventEmitter;

NotImplemented = require('../utility').NotImplemented;

Dispatcher = require('../dispatcher');

module.exports = {
  EmittingStore: Object.create(EventEmitter.prototype, {
    initialize: {
      enumerable: true,
      value: function() {
        if (this._INITIALIZED) {
          throw new Error("Store already initialized");
        } else if (this._handle_action == null) {
          throw new NotImplemented("Define _handle_action method");
        }
        Dispatcher.register((function(_this) {
          return function(action) {
            return _this._handle_action(action);
          };
        })(this));
        this._INITIALIZED = true;
        return this._post_initialize_hook();
      }
    },
    _INITIALIZED: {
      writable: true,
      value: false
    },
    _post_initialize_hook: {
      enumerable: false,
      value: function() {}
    }
  })
};
