var errors;

errors = require('../../utility').errors;

module.exports = {
  DEACTIVATE_TIMEOUT: 250,
  componentWillMount: function() {
    if (this._deactivate == null) {
      throw new errors.NotImplemented("Mixing classes should implement _deactivate");
    }
    if (this._should_deactivate == null) {
      throw new errors.NotImplemented("Mixing classes should implement _should_deactivate");
    }
  },
  componentDidUpdate: function() {
    if (this._should_deactivate()) {
      return setTimeout((function(_this) {
        return function() {
          return _this._deactivate();
        };
      })(this), this.DEACTIVATE_TIMEOUT);
    }
  }
};
