var Cell, Dispatcher, EmittingStore, GAME, GRID, GridEvolver, Immutable, LAST_CELLS, LEVEL, LIVE_CELLS, MethodKeyAction, MotionKeyAction, Ticker, XY, ref, ref1, ref2;

ref = require('../actions'), MotionKeyAction = ref.MotionKeyAction, MethodKeyAction = ref.MethodKeyAction;

ref1 = require('../utility'), Ticker = ref1.Ticker, XY = ref1.XY, GAME = ref1.GAME;

Cell = require('../views/cell');

ref2 = require('../settings'), GRID = ref2.GRID, LEVEL = ref2.LEVEL;

Dispatcher = require('../dispatcher');

EmittingStore = require('./emitter').EmittingStore;

Immutable = require('immutable');

LAST_CELLS = null;

LIVE_CELLS = Immutable.OrderedMap().withMutations(function(mutable_cells) {
  var col, i, len, range, results, row;
  range = GRID.range();
  results = [];
  for (i = 0, len = range.length; i < len; i++) {
    row = range[i];
    results.push((function() {
      var j, len1, results1;
      results1 = [];
      for (j = 0, len1 = range.length; j < len1; j++) {
        col = range[j];
        results1.push(mutable_cells.set(XY.value_of(row, col), null));
      }
      return results1;
    })());
  }
  return results;
});

GridEvolver = Object.create(EmittingStore, {
  _post_initialize_hook: {
    enumerable: false,
    value: function() {
      return this._reset();
    }
  },
  cellmap: {
    enumerable: true,
    value: function() {
      return LIVE_CELLS;
    }
  },
  add_cells_listener: {
    enumerable: true,
    value: function(listener) {
      return this.addListener(this._CHANGE_EVENT, listener);
    }
  },
  add_score_listener: {
    enumerable: true,
    value: function(listener) {
      return this.addListener(this._SCORE_EVENT, listener);
    }
  },
  _CHANGE_EVENT: {
    value: 'change'
  },
  _SCORE_EVENT: {
    value: 'score'
  },
  _METHOD_KEYMAP: {
    value: Immutable.Map([['restart', '_restart']])
  },
  _handle_action: {
    value: function(action) {
      var method, motion;
      if (action.is_a(MotionKeyAction)) {
        motion = action.motion();
        if ((motion != null) && !GAME.over()) {
          GAME.set_motion(motion);
          if (!Ticker.ticking()) {
            return this._tick();
          }
        }
      } else if (action.is_a(MethodKeyAction)) {
        method = this._METHOD_KEYMAP.get(action.method());
        return this[method]();
      }
    }
  },
  _emit_cells: {
    value: function() {
      return this.emit(this._CHANGE_EVENT, {
        cellmap: LIVE_CELLS
      });
    }
  },
  _emit_score: {
    value: function(ev) {
      return this.emit(this._SCORE_EVENT, ev);
    }
  },
  _tick: {
    value: function() {
      LAST_CELLS = LIVE_CELLS;
      this._update();
      if (GAME.out_of_bounds()) {
        GAME.track_collision(Cell.WALL);
      }
      if (GAME.should_reset_round()) {
        this._reset({
          round: true
        });
        Ticker.tick((function(_this) {
          return function() {
            return _this._tick();
          };
        })(this));
      } else if (GAME.over()) {
        this._finish();
      } else {
        Ticker.tick((function(_this) {
          return function() {
            return _this._tick();
          };
        })(this));
      }
      return this._emit_cells();
    }
  },
  _reset: {
    value: function(options) {
      if (options == null) {
        options = {
          round: false
        };
      }
      if (options.round) {
        GAME.reset_round();
      } else {
        GAME.reset();
      }
      this._batch_mutate(function(mutable_cells) {
        return mutable_cells.forEach(function(cell, xy) {
          if (GAME.collides_with(xy)) {
            return mutable_cells.set(xy, Cell.SNAKE);
          } else {
            return mutable_cells.set(xy, Cell.VOID);
          }
        });
      });
      LAST_CELLS = null;
      LIVE_CELLS = LEVEL.random_reset(LIVE_CELLS);
      LIVE_CELLS.entrySeq().forEach(function(entry) {
        var __, cell;
        __ = entry[0], cell = entry[1];
        if (cell === Cell.ITEM) {
          return GAME.add_item();
        }
      });
      if (!options.round) {
        this._emit_score({
          reset: true
        });
        return this._emit_cells();
      }
    }
  },
  _finish: {
    value: function() {
      var transform_to_cell;
      if (GAME.failed()) {
        transform_to_cell = Cell.COLLISION;
        this._rewind();
      } else {
        transform_to_cell = Cell.ITEM;
      }
      return this._batch_mutate(function(mutable_cells) {
        return mutable_cells.forEach(function(cell, xy) {
          if (cell === Cell.SNAKE) {
            return mutable_cells.set(xy, transform_to_cell);
          }
        });
      });
    }
  },
  _batch_mutate: {
    value: function(mutator) {
      LAST_CELLS = LIVE_CELLS;
      return LIVE_CELLS = LIVE_CELLS.withMutations(mutator);
    }
  },
  _rewind: {
    value: function() {
      LIVE_CELLS = LAST_CELLS;
      return LAST_CELLS = null;
    }
  },
  _update: {
    value: function() {
      GAME.move_snake();
      return this._batch_mutate((function(_this) {
        return function(mutable_cells) {
          return mutable_cells.forEach(function(previous_cell, xy) {
            if (GAME.collides_with(xy)) {
              if (previous_cell === Cell.VOID) {
                return mutable_cells.set(xy, Cell.SNAKE);
              } else {
                if (previous_cell === Cell.ITEM) {
                  _this._emit_score({
                    increment: true
                  });
                  mutable_cells.set(xy, Cell.SNAKE);
                }
                return GAME.track_collision(previous_cell, xy);
              }
            } else if (previous_cell === Cell.SNAKE) {
              return mutable_cells.set(xy, Cell.VOID);
            }
          });
        };
      })(this));
    }
  },
  _restart: {
    value: function() {
      return Ticker.stop((function(_this) {
        return function() {
          return _this._reset();
        };
      })(this));
    }
  }
});

module.exports = GridEvolver;
