var Action, Immutable, KeyDownAction, MethodKeyAction, MotionKeyAction, XY, _,
  extend = function(child, parent) { for (var key in parent) { if (hasProp.call(parent, key)) child[key] = parent[key]; } function ctor() { this.constructor = child; } ctor.prototype = parent.prototype; child.prototype = new ctor(); child.__super__ = parent.prototype; return child; },
  hasProp = {}.hasOwnProperty;

Action = require('./action');

_ = require('underscore');

Immutable = require('immutable');

XY = require('../utility').XY;

KeyDownAction = (function(superClass) {
  extend(KeyDownAction, superClass);

  function KeyDownAction() {
    return KeyDownAction.__super__.constructor.apply(this, arguments);
  }

  KeyDownAction.KEYCODES = Immutable.Set([37, 38, 39, 40, 82]);

  KeyDownAction.validate_payload = function(payload) {
    if (!(_.isObject(payload) && _.isNumber(payload.keycode))) {
      throw new Error('Expected payload to have a number keycode property');
    }
  };

  KeyDownAction.post_value_of_hook = function(payload) {
    var keycode;
    keycode = payload.keycode;
    if (MotionKeyAction.KEYCODE_MAP.has(keycode)) {
      return new MotionKeyAction(payload);
    } else if (MethodKeyAction.KEYCODE_MAP.has(keycode)) {
      return new MethodKeyAction(payload);
    } else {
      throw new Error("Unsupported keycode: " + keycode + "; try " + this.KEYCODES);
    }
  };

  KeyDownAction.prototype.keycode = function() {
    return this.payload.keycode;
  };

  return KeyDownAction;

})(Action);

MotionKeyAction = (function(superClass) {
  extend(MotionKeyAction, superClass);

  function MotionKeyAction() {
    return MotionKeyAction.__super__.constructor.apply(this, arguments);
  }

  MotionKeyAction.KEYCODE_MAP = Immutable.Map([[37, XY.left()], [38, XY.up()], [39, XY.right()], [40, XY.down()]]);

  MotionKeyAction.prototype.motion = function() {
    return this.constructor.KEYCODE_MAP.get(this.payload.keycode) || null;
  };

  return MotionKeyAction;

})(KeyDownAction);

MethodKeyAction = (function(superClass) {
  extend(MethodKeyAction, superClass);

  function MethodKeyAction() {
    return MethodKeyAction.__super__.constructor.apply(this, arguments);
  }

  MethodKeyAction.KEYCODE_MAP = Immutable.Map([[82, 'restart']]);

  MethodKeyAction.prototype.method = function() {
    return this.constructor.KEYCODE_MAP.get(this.payload.keycode) || null;
  };

  return MethodKeyAction;

})(KeyDownAction);

module.exports = {
  KeyDownAction: KeyDownAction,
  MotionKeyAction: MotionKeyAction,
  MethodKeyAction: MethodKeyAction
};
