var $, ARROW_KEYS, GRID, Grid, GridEvolver, KEYCODES, KeySender, Keys, PressedKeys, RESTART_KEY, SCOREBOARD, Score, ref, ref1, ref2;

ref = require('./stores'), GridEvolver = ref.GridEvolver, PressedKeys = ref.PressedKeys;

ref1 = require('./actions'), KeySender = ref1.KeySender, KEYCODES = ref1.KEYCODES;

ref2 = require('./views'), Grid = ref2.Grid, Keys = ref2.Keys, Score = ref2.Score;

$ = window.$;

if ($ != null) {
  GRID = $('#mamba')[0];
  SCOREBOARD = $('#scoreboard')[0];
  ARROW_KEYS = $('#arrow-keys-container')[0];
  RESTART_KEY = $('#restart-key-container')[0];
  KeySender.initialize({
    jQuery: $
  }).listen(KEYCODES, {
    prevent_default: true
  });
  PressedKeys.initialize();
  GridEvolver.initialize();
  Grid.mount(GRID).render();
  Score.mount(SCOREBOARD).render();
  Keys.mount_arrow_keys(ARROW_KEYS).mount_restart_key(RESTART_KEY).render_keys();
} else {
  throw new Error("Couldn't find window.$, are you sure jQuery is loaded?");
}
