var Deactivates, Key, Keys, PressedKeys, React, RestartKey;

React = require('react');

Keys = require('./types');

Key = require('./key');

Deactivates = require('./deactivates');

PressedKeys = require('../../stores').PressedKeys;

RestartKey = React.createClass({
  mixins: [Deactivates],
  getInitialState: function() {
    return {
      active: false
    };
  },
  componentWillMount: function() {
    return PressedKeys.add_change_listener((function(_this) {
      return function(arg) {
        var keycode;
        keycode = arg.keycode;
        return _this._on_change({
          keycode: keycode
        });
      };
    })(this));
  },
  _should_deactivate: function() {
    return this.state.active === true;
  },
  _deactivate: function() {
    return this.setState({
      active: false
    });
  },
  _on_change: function(arg) {
    var keycode;
    keycode = arg.keycode;
    if (Keys.R.keycode() === keycode) {
      return this.setState({
        active: true
      });
    }
  },
  render: function() {
    return React.createElement(Key, {
      "pressed": this.state.active,
      "keytype": Keys.R
    });
  }
});

module.exports = RestartKey;
