var TICK, Ticker, _;

TICK = require('../settings').TICK;

_ = require('underscore');

Ticker = {

  /*
    Object whose sole responsibility is to call a function on a given interval
    (that's an application-level setting), and allow for stopping said interval.
   */
  _interval: null,
  tick: function(callback) {
    if (!_.isFunction(callback)) {
      throw new Error("Expected callback function for .tick");
    }
    return this._timeout = setTimeout(callback, TICK.interval);
  },
  stop: function(on_cleared) {
    clearTimeout(this._timeout);
    this._timeout = null;
    return typeof on_cleared === "function" ? on_cleared() : void 0;
  },
  ticking: function() {
    return this._timeout != null;
  }
};

module.exports = Ticker;
