var Dispatcher, INITIALIZED, Immutable, KeyDownAction, KeySender, MAC_COMMAND, _;

Dispatcher = require('../dispatcher');

_ = require('underscore');

Immutable = require('immutable');

KeyDownAction = require('./keydown').KeyDownAction;

INITIALIZED = false;

MAC_COMMAND = Immutable.Set([91, 93]);

KeySender = Object.create({}, {

  /*
    Object that dispatches a pre-configured collection of keycodes.
  
    jQuery is a dependency for event normalization.
   */
  dependencies: {
    enumerable: true,
    value: Immutable.Map([['jQuery', '$']])
  },

  /*
    Initialize with the required dependencies, and start listening for keys.
  
    @param {object} dependencies -
      an object with key-value pairs of dependency names to dependency values
      (no type-checking is done with the values).
   */
  initialize: {
    enumerable: true,
    value: function(dependencies) {
      if (INITIALIZED) {
        this;
      } else {
        if (!_.isObject(dependencies)) {
          throw new Error('Pass an object with required dependencies');
        }
        this.dependencies.entrySeq().forEach((function(_this) {
          return function(entry) {
            var external_name, internal_name;
            external_name = entry[0], internal_name = entry[1];
            if (!_.has(dependencies, external_name)) {
              throw new Error("Couldn't find dependency " + external_name);
            }
            return _this[internal_name] = dependencies[external_name];
          };
        })(this));
      }
      INITIALIZED = true;
      this.$(document).keyup((function(_this) {
        return function(ev) {
          return _this._clear_pressed_keys();
        };
      })(this));
      return this;
    }
  },

  /*
    Listen for a collection of keycodes, and emit the KeyDownAction when caught.
  
    @param {array} keycodes -
      an array of numerical keycodes to listen for.
    @param {object} options -
      an options dictionary; currently only prevent_default is supported,
      defaulting to false. when it's true and ALT, CTRL, SHIFT, or the Mac
      COMMAND keys aren't pressed, the keydown event won't be propagated
      after dispatching the KeyDownAction.
   */
  listen: {
    enumerable: true,
    value: function(keycodes, options) {
      this._validate_listen(keycodes, options);
      keycodes = Immutable.Set(keycodes);
      options = _.defaults(options, {
        prevent_default: false
      });
      return this.$(document).keydown((function(_this) {
        return function(ev) {
          var bubbled, keycode;
          bubbled = ev;
          keycode = ev.which;
          _this._add_pressed_key(keycode);
          if (keycodes.has(keycode)) {
            Dispatcher.dispatch(KeyDownAction.value_of({
              keycode: keycode
            }));
            if (_this._should_prevent_default(ev, options)) {
              bubbled = false;
            }
          }
          return bubbled;
        };
      })(this));
    }
  },
  _should_prevent_default: {
    value: function(ev, options) {
      if (options.prevent_default) {
        if (this._pressed_keys.intersect(MAC_COMMAND).size > 0) {
          return false;
        } else if (ev.altKey || ev.ctrlKey || ev.shiftKey) {
          return false;
        } else {
          return true;
        }
      } else {
        return false;
      }
    }
  },
  _pressed_keys: {
    writable: true,
    value: Immutable.Set()
  },
  _add_pressed_key: {
    value: function(keycode) {
      return this._pressed_keys = this._pressed_keys.add(keycode);
    }
  },
  _clear_pressed_keys: {
    value: function() {
      return this._pressed_keys = this._pressed_keys.clear();
    }
  },
  _validate_listen: {
    value: function(keycodes, options) {
      if (INITIALIZED == null) {
        throw new Error("Didn't properly initialize; call .initialize first!");
      }
      if (!(keycodes instanceof Immutable.Iterable || Array.isArray(keycodes))) {
        throw new Error("keycodes Immutable.Iterable/array required");
      }
      if ((options != null) && !_.isObject(options)) {
        throw new Error("options argument should be an object");
      }
    }
  }
});

module.exports = KeySender;
